.header,
.header > span {
  position: relative;
  background-size: 100% 100%;
  text-align: center;
  color: #fff;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 0.25rem;
}
.header{
  background: url(../assets/images/top.png) no-repeat;
}
.left,
.right {
  position: absolute;
  background-size: 100% 100%;
  height: 0.03125rem;
  width: 0.7291666667rem;
  top: 0;
}
span,
.left-font,
.right-font {
  background-image: -webkit-linear-gradient(left, #006df5, #00eaff, #01a9fd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.left {
  background: url(../assets/images/left-top.png);
  left: 1.1927083333rem;
  transform: translateY(-50%);
}
.left-font,
.right-font {
  position: absolute;
  color: #fff;
  font-size: 0.1458333333rem;
  top: 50%;
  transform: translateY(-50%);
}
.left-font {
  left: 0.7291666667rem;
}
.right-font {
  right: 0.5520833333rem;
}
.right {
  background: url(../assets/images//right-top.png);
  right: 1.1927083333rem;
  transform: translateY(-50%);
}
