@charset "utf-8";

/********** Global **********/
/*
 *常用背景色： #0b0f34 (6,64,102) (29,45,57) (7,33,58) (8,13,28) (15,43,36)
 */
* {
  margin: 0;
  padding: 0;
  font-size: 0.0833333333rem;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  min-height: 635px;
  font-family: 'microsoft yahei', arial, sans-serif;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

body {
  background-image: url('../assets/images/bg.png');
}

table {
  height: 100%;
  overflow-y: auto;
  display: block;
}

#mainMap canvas {
  width: 105% !important;
}

tr {
  padding: 8px;
}

td {
  color: #c9ccd4;
  padding: 8px;
}

.content_title {
  width: 200px;
  text-align: center;
  color: #01c4f7;
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -100px;
}

.databg {
  background: url('../assets/images/databg.png') 0 0 no-repeat;
  background-size: 22px 30px;
  height: 30px;
  width: 22px;
  color: #fff;
  font-size: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}

@keyframes bganomation {
  0% {
    width: 50%;
  }

  50% {
    width: 0%;
  }

  100% {
    width: 50%;
  }
}

@keyframes rotate1 {
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes retate2 {
  100% {
    transform: translate(-50%, -50%) rotate(-360deg);
  }
}

.map_bg {
  position: absolute;
  top: 54%;
  left: 50%;
  width: 45.8vh;
  height: 45.8vh;
  min-width: 280px;
  min-height: 280px;
  transform: translate(-50%, -50%);
  background: url('../assets/images/map.png') center no-repeat;
  background-size: 45.8vh 45.8vh;
  opacity: 0.5;
}

.circle_allow {
  position: absolute;
  top: 54%;
  left: 50%;
  width: 50vh;
  height: 50vh;
  min-width: 280px;
  min-height: 280px;
  transform: translate(-50%, -50%);
  background: url('../assets/images/jt.png') center no-repeat;
  background-size: 50vh 50vh;
  animation: retate2 10s linear infinite;
}

.circle_bg {
  position: absolute;
  top: 54%;
  left: 50%;
  width: 52vh;
  height: 52vh;
  transform: translate(-50%, -50%);
  background: url('../assets/images/lbx.png') center no-repeat;
  background-size: 52vh 52vh;
  animation: rotate1 10s linear infinite;
  opacity: 0.4;
}

.left_bg {
  width: 62%;
  height: 100%;
  position: absolute;
  right: 0;
  background-position: right;
  background: url('../assets/images/left_header.png') 0 10px no-repeat;
}

.right_bg {
  width: 53%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: url('../assets/images/right_header.png') 0 10px no-repeat;
}

.header_main {
  margin: 0 auto;
  width: 100%;
  height: 65px;
  max-width: 1920px;
  /* // background:url("./assets/images/header.png") center no-repeat; */
  position: relative;
}

.header_main h3 {
  margin: 0 auto;
  padding-top: 14px;
  line-height: 50px;
  text-align: center;
  font-size: 24px;
  color: rgb(255, 255, 255);
}

.wrapper {
  position: absolute;
  top: 80px;
  bottom: 0;
  left: 0;
  right: 0;
  min-height: 555px;
}

.container-fluid {
  height: 100%;
  min-height: 100%;
}

.row {
  margin-left: -7px;
  margin-right: -8px;
}

.row>div {
  padding-left: 7px;
  padding-right: 8px;
}

.xpanel-wrapper {
  padding-bottom: 15px;
  box-sizing: border-box;
}

.xpanel-wrapper-1 {
  height: 100%;
}

.xpanel-wrapper-2 {
  height: 50%;
}

.xpanel-wrapper-3 {
  height: 33.33333%;
}

.xpanel-wrapper-4 {
  height: 40%;
}

.xpanel-wrapper-5 {
  height: 60%;
}

.xpanel-wrapper-6 {
  height: 30%;
}

.xpanel-wrapper-7 {
  height: 30%;
}

.xpanel-wrapper-8 {
  height: 70%;
}

.xpanel {
  padding: 15px;
  height: 100%;
  min-height: 170px;
  background-size: 100% 100%;
  box-sizing: border-box;
}

/* tool */
.fill-h {
  height: 100%;
  min-height: 100%;
}

.no-margin {
  margin: 0 !important;
}

.no-padding {
  padding: 0 !important;
}

/* scrollbar */
::-webkit-scrollbar {
  width: 0;
  height: 0;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.3);
}

.bottom-body,
.search {
  background: url(../assets/images/pl.png);
  margin: 0 0.0520833333rem;
  display: flex;
  align-items: center;
}

.lx6 {
  background: url(../assets/images/lx6.png) no-repeat;
  width: 138px;
  height: 100px;
  right: 42%;
  bottom: 78%;
  position: absolute;
}

.lx7 {
  background: url(../assets/images/lx7.png) no-repeat;
  width: 138px;
  height: 100px;
  right: 45%;
    bottom: 64%;
  position: absolute;
}

.lx8 {
  background: url(../assets/images/lx8.png) no-repeat;
  width: 138px;
  height: 100px;
  right: 42%;
  bottom: 62%;
  position: absolute;
}

.bottom-body .flex-1 {
  flex: 1;
  text-align: center;
  border-right: 1px dashed #00286a;
}

.bottom-body .flex-1:last-child {
  border-right: none;
}

.xpanel-p-0 {
  padding-top: 0;
  padding-bottom: 0;
}

.bottom-body-tj {
  display: flex;
  margin: 0 0.0520833333rem;
  height: 30%;
  padding: 0.1302083333rem 0.0520833333rem;
}

.bottom-body-tj>div {
  flex: 1;
  margin-right: 0.15625rem;
}

.bottom-body .common-flex {
  height: 35%;
}

.payOrder {
  display: none;
}

.bottom-left {
  background: url(../assets/images/bottom-left.png) no-repeat;
  background-size: cover;
  cursor: pointer;
  width: 1.40625rem;
}

.bottom-center {
  background: url(../assets/images/bottom-center.png) no-repeat;
  background-size: cover;
  cursor: pointer;
  width: 1.40625rem;
}

.bottom-right {
  background: url(../assets/images/bottom-right.png) no-repeat;
  background-size: cover;
  cursor: pointer;
  width: 1.40625rem;
}

.relative {
  text-align: left;
  position: relative;
  left: 25%;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
}

.relative .head-font {
  font-size: 0.125rem;
  font-weight: bold;
}

.relative .body-font span {
  font-size: 0.0729166667rem;
}

.moods {
  color: #eea754;
  margin-left: 0.0520833333rem;
}

.common-flex {
  display: flex;
}

.consume {
  font-size: 0.0729166667rem;
  color: #5274ef;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.consume>div {
  display: flex;
}

.price {
  font-size: 0.09375rem;
  color: #ffb026;
}

.consume-group {
  flex: 1;
}

.text-right {
  text-align: right;
}

.nav {
  display: flex;
  justify-content: center;
  margin: 0.0520833333rem 0;
}

.nav>div {
  font-size: 0.0729166667rem;
  color: #5274ef;
  margin: 0 0.0520833333rem;
  cursor: pointer;
}

.active {
  color: #fff !important;
}

.search>input {
  background: transparent;
  border: none;
  caret-color: #fff;
  outline: none;
  /*去掉选中状态边框*/
  height: 0.25rem;
  color: #2aa7d3;
}

.search {
  padding: 5px;
}

.search-img {
  margin: 0 0.078125rem;
  width: 0.1458333333rem;
  height: 0.1458333333rem;
  background: url(../assets/images/search.png);
  background-size: contain;
}

.search>input::placeholder {
  color: #2aa7d3;
}

.search-pl {
  height: 0.625rem;
  color: #2aa7d3;

  overflow: hidden;
  overflow-y: scroll;
}

.search-pl div {
  font-size: 0.1041666667rem;
}

.pl-content {
  margin-left: 0.0520833333rem;
}

.common-white {
  color: #fff;
}

.common-o {
  color: #dd9b52;
}

.writ-info-nav {
  display: flex;
}

.writ-info-nav>div {
  cursor: pointer;
  color: #fff;
  text-align: center;
  padding: 0 0.0260416667rem;
  margin: 0 0.0520833333rem;
  display: flex;
  flex-direction: column;
}

.writ-info-nav>div>div {
  margin-bottom: 0.0260416667rem;
}

.wActive {
  height: 3px;
  display: inline-block;
  width: 100%;
  padding: 0 0.0104166667rem;
  background-image: linear-gradient(to right, #255599, #0ec29c);
  animation: bdw 0.2s ease-in;
}

@keyframes bdw {
  0% {
    width: 0%;
  }

  50% {
    width: 50%;
  }

  100% {
    width: 100%;
  }
}

.writ-info-content {
  color: #45aeff;
}

.writ-info-content>div {
  height: 0.1875rem;
  margin-top: 0.1041666667rem;
}

.order-search {
  background: #1664c5;
  border: 1px solid #167ac5;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
}

.writ-info-content>div img {
  vertical-align: middle;
  height: 0.1875rem;
  width: 0.1875rem;
  margin: 0 0.1041666667rem;
}

.writ-info-content>div:first-child {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.writ-info-content>div input {
  background: transparent;
  border: none;
  border-bottom: 1px solid #333462;
  caret-color: #fff;
  outline: none;
  /*去掉选中状态边框*/
  color: #2aa7d3;
  font-size: 0.1041666667rem;
  font-weight: bold;
  width: 40%;
  height: 0.1875rem;
  text-align: center;
  padding-bottom: 0.0520833333rem;
}

.writ-info-content>div:nth-child(2) {
  display: flex;
  margin-left: 3%;
  align-items: flex-end;
}

.writ-info-content>div:nth-child(2) span {
  border-bottom: 1px solid #333462;
  padding-bottom: 0.078125rem;
  margin-left: 0.1041666667rem;
  width: 30%;
}

.back {
  position: absolute;
  right: 5%;
  top: 10%;
  cursor: pointer;
}

.back:hover {
  color: #fff;
}

.back-content {
  display: block !important;
  height: 0.6770833333rem !important;
  overflow: hidden;
  overflow-y: auto;
}

.back-content>div {
  padding: 0.0520833333rem 0.0260416667rem;
  border-bottom: 1px solid #ccc;
}

.dot {
  width: .0729166667rem;
  height: .0729166667rem;
  background: #000;
  display: inline-block;
  border-radius: 100%;
}